import React, { useEffect, useState, useRef } from "react";
import { reduxForm } from "redux-form";
import { Input, createField, ImageField } from "../FormsControls/FormsControls";
import {
  acceptableName,
  maxLength200,
  acceptablePrice,
  acceptableDiscount,
  acceptableDiscountEndDate,
  validate,
  imageFormat,
  imageWidth_200_4000,
  imageHeight_200_4000,
} from "../FormsControls/validators";
import { connect } from "react-redux";
import { getProduct, updateProduct } from "../../redux/product-reducer";
import styles from "../FormsControls/FormsControls.module.scss";
import productStyles from "./Product.module.scss";
import { withRouter } from "react-router-dom";
import { compose } from "redux";
import Preloader from "../Preloader/Preloader";
import withAuthRedirect from "../HOCS/withAuthRedirect";

// can show the summary error
// form-level validation
const UpdateProductForm = (props) => {
  const [productState, setPS] = useState(
    Object.values(props.product).reduce(
      (acum, val) => ((acum[val] = false), acum),
      {}
    )
  );
  const { handleSubmit, pristine, reset, submitting, error, product } = props;

  const activate = (id) => {
    setPS({ ...productState, [id]: true });
  };

  // const [newProduct, setNewProduct] = useState({});
  const setNewProductR = useRef({});
  function deactivate(id) {
    if (
      setNewProductR.current &&
      setNewProductR.current.value !== void 0 &&
      setNewProductR.current.value !== ""
    ) {
      // setNewProduct((prods) => ({
      //   ...prods,
      //   [setNewProductR.current.name]:
      //     setNewProductR.current.value || undefined,
      // }));
      setNewProductR.current = void 0;
    } else {
      setPS({ ...productState, [id]: false });
    }
  }

  const namelabel = "Наименование товара";
  const photolabelOld = "Текущее фото товара";
  const photolabel = "Новое фото товара";
  const desclabel = "Описание товара";
  const pricelabel = "Цена товара";
  const disclabel = "Скидка";
  const discountEndTimeabel = "Дата окончания акции";

  const fieldsNames = [
    "name",
    "photo",
    "description",
    "price",
    "discount",
    "discountEndTime",
  ];

  const generOwnProps = (fieldsName) => ({
    onBlur: () => {
      deactivate(fieldsName);
    },
    autoFocus: true,
    refa: setNewProductR,
  });
  return (
    <form onSubmit={handleSubmit} className={styles.wholeForm}>
      {/* key, name, price, discount, discountEndTime, photo */}
      <div className={productStyles.fieldDescr}>{namelabel}</div>
      {productState[fieldsNames[0]] ? (
        createField(
          `${product.name}`,
          fieldsNames[0],
          Input,
          [acceptableName],
          {
            ...generOwnProps(fieldsNames[0]),
          }
        )
      ) : (
        <div id={fieldsNames[0]} onDoubleClick={() => activate(fieldsNames[0])}>
          {product.name}
        </div>
      )}
      <div className={productStyles.fieldDescr}>{photolabelOld}</div>
      <div className={productStyles.imgMiddleContainer}>
        <img src={product.photo} alt="Data" />
      </div>

      <div className={productStyles.fieldDescr}>{photolabel}</div>
      {createField(
        "Загрузите фото ...",
        fieldsNames[1],
        ImageField,
        [imageFormat, imageWidth_200_4000, imageHeight_200_4000],
        {
          mimeType: "image/jpeg, image/png",
          maxWidth: 4000,
          minHeight: 200,
          minWidth: 200,
          maxHeight: 4000,
          alt: "Фото товара",

          ...generOwnProps(fieldsNames[1]),
        }
      )}

      <div className={productStyles.fieldDescr}>{desclabel}</div>
      {productState[fieldsNames[2]] ? (
        createField(
          `${product.description || "..."}`,
          fieldsNames[2],
          Input,
          [maxLength200],
          {
            ...generOwnProps(fieldsNames[2]),
          }
        )
      ) : (
        <div onDoubleClick={() => activate(fieldsNames[2])}>
          {product.description || "..."}
        </div>
      )}

      <div className={productStyles.fieldDescr}>{pricelabel}</div>
      {productState[fieldsNames[3]] ? (
        createField(
          `${product.price}`,
          fieldsNames[3],
          Input,
          [acceptablePrice],
          { ...generOwnProps(fieldsNames[3]) }
        )
      ) : (
        <div onDoubleClick={() => activate(fieldsNames[3])}>
          {product.price}
        </div>
      )}

      <div className={productStyles.fieldDescr}>{disclabel}</div>
      {productState[fieldsNames[4]] ? (
        createField(
          `${product.discount || "..."}`,
          fieldsNames[4],
          Input,
          [acceptableDiscount],
          {
            ...generOwnProps(fieldsNames[4]),
          }
        )
      ) : (
        <div onDoubleClick={() => activate(fieldsNames[4])}>
          {product.discount || "..."}
        </div>
      )}

      <div className={productStyles.fieldDescr}>{discountEndTimeabel}</div>
      {productState[fieldsNames[5]] ? (
        createField(
          `${product.discountEndTime || "..."}`,
          fieldsNames[5],
          Input,
          [acceptableDiscountEndDate],
          { ...generOwnProps(fieldsNames[5]) }
        )
      ) : (
        <div onDoubleClick={() => activate(fieldsNames[5])}>
          {product.discountEndTime || "..."}
        </div>
      )}
      {error && <div className={styles.formSummaryError}>{error}</div>}
      <div className="">
        <button type="submit" disabled={pristine || submitting}>
          Обновить
        </button>
      </div>
      <div className="">
        <button type="button" disabled={pristine || submitting} onClick={reset}>
          Очистить значения
        </button>
      </div>
    </form>
  );
};

const UpdateProductReduxForm = reduxForm({
  form: "updateProduct",
  validate,
})(UpdateProductForm);

const UpdateProduct = ({
  updateProduct,
  getProduct,
  currentProduct,
  isFetching,
  ...props
}) => {
  const [hasCP, setCP] = useState(false);

  //  да тут много не DRY-я,  я просто не успел вынести в кастомные хуки(
  const refUpdated = useRef();

  useEffect(() => {
    if (refUpdated.current) {
      props.history.push("/catalog");
    }
  }, [isFetching]);

  const onSubmit = async (formData) => {
    if (formData.photo) {
      formData = { ...formData, newPhoto: true };
    }
    let newProd = { ...currentProduct, ...formData };

    console.log(currentProduct);
    await updateProduct(currentProduct.key, newProd);
    refUpdated.current = true;
  };

  useEffect(() => {
    const fetchProduct = async () => {
      let productKey = props.match.params.productKey; // productKey
      if (!productKey) {
        //   <Redirect to="/catalog" />;
        props.history.push("/catalog");
      } else {
        await getProduct(productKey);
        setCP(true);
      }
    };
    if (!hasCP) {
      fetchProduct();
    }
  }, [props.match.params.key, getProduct, props.history, hasCP]);

  if (!hasCP) return <Preloader />;
  return (
    <>
      <div className={productStyles.productOperationContainer}>
        <h1 className={productStyles.productOperationHeader}>Обновить товар</h1>
        <UpdateProductReduxForm onSubmit={onSubmit} product={currentProduct} />
      </div>
    </>
  );
};

const mapStateToProps = (state) => ({
  isFetching: state.products.isFetching,
  currentProduct: state.products.selectedProduct,
  // productPhotoUrl: state.products.prod
});

export default compose(
  withAuthRedirect,
  connect(mapStateToProps, { getProduct, updateProduct }),
  withRouter
)(UpdateProduct);
